#h1-redes{font-size:30px}.sub-contenedor-redes{width:100%;height:60vh;display:flex;flex-direction:column;margin-bottom:25px;align-items:flex-start}.sub-contenedor-redes div:nth-child(even){display:flex;justify-content:flex-end}.sub-contenedor-redes .contenedor-circulos-redes{width:70%;margin:0 auto;height:12vh;display:flex}.sub-contenedor-redes .contenedor-circulos-redes .circulos-redes{width:70px;height:100%;background-position:left;background-size:contain;background-repeat:no-repeat;transition:.8s}#github{background-image:url("../../imagenes/github_r.png");animation:rotación 30s linear 1s infinite reverse none}#instagram{background-image:url("../../imagenes/instagram_r.png");animation:rotación 45s linear 3s infinite reverse none}#twitter{background-image:url("../../imagenes/twitter_r.png");animation:rotación 35s linear 0s infinite normal none}#linkedin{background-image:url("../../imagenes/linkedin_r.png");animation:rotación 50s linear 0.5s infinite normal none}#mail{background-image:url("../../imagenes/mail_r.png");animation:rotación 40s linear 2s infinite reverse none}@keyframes rotación{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}#github:hover,#instagram:hover,#twitter:hover,#linkedin:hover,#mail:hover{animation:none;transform:scale(1.1)}#instagram,#linkedin{background-position:right}@media screen and (min-width: 499px){.sub-contenedor-redes{height:65vh}.sub-contenedor-redes .contenedor-circulos-redes{height:13vh}.sub-contenedor-redes .contenedor-circulos-redes .circulos-redes{width:93px;height:100%}}@media screen and (min-width: 1200px){#h1-redes{font-size:68px}.sub-contenedor-redes{height:50vh;margin-top:5vh;flex-direction:row}.sub-contenedor-redes div:nth-child(even){display:flex}.sub-contenedor-redes .contenedor-circulos-redes{width:13%;margin:0 auto;height:50vh;display:flex;flex-direction:column;justify-content:flex-start}.sub-contenedor-redes .contenedor-circulos-redes .circulos-redes{width:100%;height:150px}}